.grid-container {
    display: grid;
    grid-template-columns: auto auto;
    background-color: rgb(51, 51, 255);
    padding: 10px;
}

.grid-item {
    background-color: rgba(255, 255, 255, 0.8);
    border: 1px solid rgba(0, 0, 0, 0.8);
    padding: 20px;
    font-size: 30px;
    text-align: center;
    margin: 10px;
}